body {
    background: #efefef;
    margin: 0;
  }
  a {
    color: inherit;
    text-decoration: none;
  }
  * {
    margin: 0;
    padding: 0;
  }
  hr{
    height: 0;
    border: none;
    border-top: 1px solid solid #DEDEDE;
  }
  ul,ol{
    list-style: none;
  }
  h1,h2,h3,h4,h5,h6{
    font-weight: normal;
  }
  .clearfix::after {
    content: '';
    display: block;
    clear: both;
  }

  [data-x].active{
    outline: 10px solid red;
  }
  [data-x].offset{
    transform: translateY(0);
    transition: all 0.5s;
  }
  @keyframes slideUp{
   0%{
     transform: translateY(-30px);
   }
   100%{
     transform: translateY(0);
   }
  }
  .topNavBar {
    padding: 20px 0 20px 0;
    position: fixed;
    top: 0;
    left: 0;
    width: 100%;
    transition: all 0.5s;
    color: #b7b7b7;
  }
  .topNavBar.sticky{
    background:white;
    padding:10px 0;
    z-index: 1;
    box-shadow: 0 0 10px rgba(0,0,0,0.25);
  }
  .topNavBar-inner {
    padding: 0 16px;
  }
  .topNavBar nav {
    padding-top: 5px;
  }
  .topNavBar nav > ul {
    list-style: none;
    margin: 0;
    padding: 0;
  }
  .topNavBar nav > ul > li {
    float: left;
    margin-left: 17px;
    margin-right: 17px;
    position: relative;
  }
  .topNavBar nav > ul > li > a { font-size: 12px; color: inherit; font-weight: bold; border-top: 3px solid transparent; border-bottom: 3px solid transparent;
    padding-top: 5px; padding-bottom: 5px; display: block; }
  .topNavBar nav > ul > li > a:hover { border-bottom: 3px solid #e06567; 
    padding-top: 5px; padding-bottom: 5px; display: block; position: relative;}
  .topNavBar nav > ul > li.active> a::after,
  .topNavBar nav > ul > li.hilight> a::after {
    content:'';
    display: block;
    position: absolute;
    top:100%;
    left:0;
    width:100%;
    background: red;
    height: 3px;
    animation: menuSlide 0.3s;
   }
  @keyframes menuSlide{
    0%{
      width:0;
    }
    100%{
      width:100%;
    }
  }
  .topNavBar .submenu{
    display: none;
    position: absolute;
    right:0;
    top:100%;
    background: white;
    color:#3d4451;
    box-shadow: 0 0 5px rgba(0,0,0,0)
  }
  .topNavBar li.active> .submenu{
    display: block;
    animation:submenuSlide .3s;
  }
  @keyframes submenuSlide{
    0%{
      margin-right: 100%;
    }
    100%{
      margin-right: 0%;
    }
  }
  .topNavBar .submenu > li{
    white-space: nowrap;
    padding: 5px 10px;
  }
  .topNavBar .logo {
    font-size: 24px;
    font-family: 'Arial Black';
    padding-top: 3px;
    padding-bottom: 4px;
  }
  .topNavBar .logo .rs {
    margin-right: 4px;
    color: #e6686a;
  }
  .topNavBar .logo .card {
    color: #9a9da2;
  }
  .banner {
    height: 515px;
    background-image: url(./img/rs-cover.jpg);
    background-position: center center;
    background-size: cover;
  }
  .banner .mask {
    height: 515px;
    background-color: rgba(0, 0, 0, 0.8);
  }
  
  .userCard {
    max-width: 940px;
    margin-left: auto;
    margin-right: auto;
    background-color: #ffffff;
    box-shadow: 0px 1px 6px 0px rgba(0,0,0,0.61);
  }
  .userCard .welcome {
    background: #e6686a;
    color: white;
    display: inline-block;
    padding: 4px 16px;
    line-height: 22px;
    position: relative;
    margin-bottom: 10px;
  }
  .userCard .welcome .triangle {
    display: block;
    border: 10px solid transparent;
    width: 0px;
    border-left-color: #e6686a;
    border-top-width: 0px;
    position: absolute;
    left: 4px;
    top: 100%;
  }
  .userCard .picture {
    float: left;
  }
  .userCard .text {
    float: left;
    margin-left: 65px;
    width: 470px;
  }
  .userCard .text h1{
    margin-top: 18px;
  }
  .userCard .text hr{
    margin: 20px 0;
  }
  .userCard .pictureAndText {
    padding: 50px;
  }
  .userCard dl dt,
  .userCard dl dd{
    float: left;
    padding: 5px 0;
  }
  .userCard dl dt{
    width: 30%;
    font-weight: bold;
  }
  .userCard dl dd{
    width: 70%;
    color: #9da0a7;
  }
  
  .userCard > footer.media{
    background: #E6686A;
    text-align: center;
  }
  .userCard > footer.media > a{
    display: inline-block;
    width: 40px;
    line-height: 30px;
    padding: 5px 0;
    border-radius: 50%;
    margin: 16px;
  }
  .userCard > footer.media > a:hover{
    background: #CF5D5F;
  }
  .userCard svg{
    width: 30px;
    height: 30px; 
    fill: white;
    vertical-align: top;
  }

  body> main{
    margin-top:-350px
  }

  body> main .download-wrapper{
    text-align: center
  }

  body> main .download{
   font-size: 14px;
   padding:21px 55px;
   line-height: 16px;
   display: inline-block;
   border:1px solid #cdcfd1;
   background-color: #efefef;
   border-radius: 2px;
   color: #3d4451;
   font-weight: bold;
   margin:32px 0;
   transition: box-shadow 0.1s;
  }

  body >main .download:hover{
    box-shadow: 0px 0px 13px 0px rgba(0,0,0,0.3);
  }

  .selfintroduction{
    max-width: 940px;
    margin-left: auto;
    margin-right: auto;
    text-align: center;
    font-family: kai;
    line-height: 1.8;
    font-size: 18px;
  }

  section.skills,
  section.portfolio {
    max-width: 940px;
    margin-left: auto;
    margin-right: auto;
    margin-top:60px;
  }

  section.skills > h2,
  section.portfolio > h2 {
    text-align: center;
    color: black;
    font-size: 34px;
    line-height: 1.2;
    font-weight: 600;
  }

  section.skills > h3{
    font-size: 14px;
    line-height: 1.1;
    padding-right: 40px;
  }
  section.skills >ol{
    background-color: white;
    box-shadow: 0px 1px 6px 0px rgba(0,0,0,0.61);
    padding: 42px 50px 10px;
    margin-top: 30px;
  }

  section.skills > ol > li{
    float: left;
    width:48%;
    box-sizing:border-box;
    
  }
  section.skills > ol > li:nth-child(even){
    float: right;
  }
  section.skills .progressBar{
    height: 5px;
    background-color: #FAE1E1;
    border-radius: 2px;
    margin:4px 0 40px;
  }

  section.skills .progressBar>.progress{
    height: 100%;
    background: #E6686A;
    width: 20%;
    border-radius: 2px;
  }
  section.portfolio{
    text-align: center;
    margin-bottom: 100px;
  }
  section.portfolio >nav >ol{
   margin:0 auto;
   display: inline-block;
   vertical-align: top;
  }

  section.portfolio >nav >ol >li{
    float:left;
    margin-left: 40px;
    cursor: pointer;
  }
  section.portfolio >nav >ol >li:nth-child(1){
    margin-left: 0;
  }
  section.portfolio >nav{
    display: inline-block;
    vertical-align: top;
    margin-top: 32px;
  }
  section.portfolio >nav .bar{
    height: 5px;
    background-color: white;
    margin-top: 5px;
    margin-bottom: 30px;
    border-radius: 2px;
  }
  section.portfolio >nav .bar-inner{
    height: 100%;
    background-color: #E6686A;
    width: 10%;
    border-radius: 2px;
    margin-left: 100px;
    transition: all 0.3s;
  }
  section.portfolio >nav .bar.state-1 .bar-inner{
    margin-left: 0;
    width: 50px;
  }
  section.portfolio >nav .bar.state-2 .bar-inner{
    margin-left: 65px;
    width: 50px;
  }
  section.portfolio >nav .bar.state-3 .bar-inner{
    margin-left: 140px;
    width: 96px;
  }
  section.portfolio .works{
    position: relative;
  }
  section.portfolio .works >.big,
  section.portfolio .works >.small{
    position: absolute;
  }